.form {
    @include cover;
    &__title {
        color: #fff;
        &:before {
            display: none; } } }

.form-box {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    position: relative;
    padding: 32px 48px;
    @include md {
        padding: 24px; }
    &:hover {
        transform: none; }
    &:before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 400px;
        max-width: 30%;
        background: $orange;
        height: 4px; }
    &__text {
        @include rob-m;
        font-size: 24px;
        margin-bottom: 36px;
        @include md {
            font-size: 18px; } } }

.input {
    margin-bottom: 36px;
    text-align: left;
    position: relative;
    @include md {
        margin-bottom: 24px; }
    label {
        display: block;
        margin-bottom: 8px;
        @include md {
            font-size: 14px;
            margin-bottom: 4px; }
        &.error {
            font-size: 12px;
            color: red;
            position: absolute;
            top: 100%;
            left: 6px;
            display: block;
            margin-top: 2px; } }
    input {
        display: block;
        width: 100%;
        height: 44px;
        border: 1px solid $grey;
        border-radius: 100px;
        font-size: 16px;
        @include rob-r;
        padding: 0 12px;
        transition: border-color .25s ease-in-out;
        @include md {
            height: 40px; }
        &:focus {
            border-color: $orange; }
        &.error {
            border-color: #ff0000;
            box-shadow: 1px 1px 12px 1px rgba(255,0,0,.25); } }
    &_special {
        display: flex;
        align-items: center;
        position: relative;
        @include r(420) {
            flex-direction: column; }
        input {
            border-radius: 100px;
            background: transparent;
            border-color: $orange;
            color: #fff;
            padding: 0 16px;
            @include placeholder {
                color: #fff; }
            @include r(420) {
                margin-bottom: 24px; } }
        label {
            &.error {
                @include r(420) {
                    top: 38px; } } }
        .button {
            padding: 10px 20px 10px 16px;
            position: absolute;
            right: 0;
            top: 0;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            font-size: 16px;
            @include md {
                padding: 8px 16px 8px 12px; }
            @include r(420) {
                position: relative;
                border-top-left-radius: 100px;
                border-bottom-left-radius: 100px; } } } }

.form-success {
    visibility: hidden;
    opacity: 0;
    transition: all .25s ease-in-out;
    color: $orange;
    font-size: 14px;
    position: relative;
    top: 10px;
    &.is-visible {
        visibility: visible;
        opacity: 1; } }
