.modal {
    $this: &;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 999;
    padding: 48px 64px;
    visibility: hidden;
    opacity: 0;
    transition: all .25s ease-in-out;
    display: flex;
    align-items: center;
    @include md {
        padding: 32px 48px; }
    @include xs {
        padding: 24px 20px; }
    &.is-open {
        visibility: visible;
        opacity: 1; }
    &:before {
        content: "";
        display: block;
        @include coverdiv;
        background: rgba(0,0,0,.75);
        z-index: 0; }
    &_video {
        #{$this}__in {
            max-width: 960px;
            overflow: hidden;
            margin: 0 auto;
            display: flex;
            align-items: center;
            background: none;
            align-self: center;
            height: auto; } }
    &__close {
        appearance: none;
        display: block;
        width: 32px;
        height: 32px;
        font-size: 32px;
        position: absolute;
        z-index: 2;
        top: 12px;
        right: 24px;
        background: none;
        border: none;
        border-radius: 0;
        @include md {
            top: 8px;
            right: 8px; }
        @include xs {
            width: 24px;
            height: 24px;
            font-size: 24px;
            top: 4px;
            right: -2px; }
        .icon {
            fill: #fff; }
        &_special {
            top: 8px;
            right: 8px;
            @include md {
                top: 8px;
                right: 8px; }
            @include xs {
                top: 8px;
                right: 8px; } } }
    &__in {
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        flex-grow: 1;
        overflow-y: auto;
        display: flex;
        flex-wrap: wrap;
        background: #fff;
        border-radius: 4px; }
    &__left {
        width: 45%;
        border-right: 1px solid $grey-light;
        @include sm {
            width: 100%;
            border-right: 0;
            border-bottom: 1px solid $grey-light; } }
    &__right {
        width: 55%;
        @include sm {
            width: 100%; } }
    &__pic {
        background: $grey-light;
        padding-top: 24px;
        height: 250px;
        img {
            height: 100%;
            display: block;
            margin: 0 auto; } }
    &__form {
        padding: 24px 32px;
        padding: 12px 20px; }
    &__info {
        padding: 24px 32px;
        text-align: left;
        font-size: 16px;
        line-height: 1.5;
        @include xs {
            padding: 12px 20px;
            font-size: 14px; }
        p {
            margin-bottom: 24px; }
        li {
            position: relative;
            padding-left: 32px;
            margin-bottom: 16px;
            &:before {
                content: "";
                width: 24px;
                height: 24px;
                background-image: url(data:image/svg+xml;base64,);
                background-repeat: no-repeat;
                background-position: 50% 50%;
                background-size: contain;
                position: absolute;
                top: 2px;
                left: 0; } } }
    &__video {
        position: relative;
        width: 100%;
        height: 0;
        padding-top: 56.25%;
        overflow: hidden;
        // +md
        //     padding-top: 56.25%
        iframe {
            @include coverdiv; } }
    .input {
        margin-bottom: 24px; }
    .button {
        padding: 10px 24px; } }
