.advantages {
    &__boxes {
        display: flex;
        flex-wrap: wrap;
        padding-top: 36px;
        width: calc(100% + 16px); } }
.advantage {
    width: calc(20% - 16px);
    margin-right: 16px;
    @include md {
        width: calc(25% - 16px);
        margin-bottom: 16px; }
    @include r(800) {
        width: calc(50% - 16px); }
    @include xs {
        width: 100%; } }
.box {
    background: #fff;
    box-shadow: -2px 4px 24px rgba(0,0,0,.15);
    border-radius: 4px;
    padding: 32px 24px;
    text-align: center;
    transition: transform .25s ease-in-out, box-shadow .25s ease-in-out;
    &:hover {
        transform: translate3d(0, -6px, 0);
        box-shadow: -2px 10px 24px 4px rgba(0,0,0,.15); }
    &__img {
        margin-bottom: 24px; }
    &__text {
        font-size: 14px; } }
