.header {
    $this: &;
    border-bottom: 10px solid $orange;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    &__in {
        display: flex;
        height: 60px;
        @include md {
            justify-content: space-between; } }
    &__logo {
        width: 190px;
        align-self: flex-end;
        position: relative;
        top: 14px; }
    &__nav {
        flex-grow: 1;
        text-align: right;
        align-self: center;
        @include md {
            transition: opacity .25s ease-in-out;
            visibility: hidden;
            opacity: 0;
            position: fixed;
            top: 0;
            right: 0;
            width: 256px;
            height: 100%;
            background: rgba(0,0,0,.9);
            z-index: 1;
            padding-top: 100px;
            box-shadow: 2px 4px 16px rgba(0,0,0,.5); } }
    &__burger {
        display: none;
        align-self: center;
        @include md {
            position: relative;
            z-index: 2;
            display: block; } }
    &.is-open {
        #{$this}__nav {
            visibility: visible;
            opacity: 1; } } }

.nav {
    a {
        @include rob-r;
        text-decoration: none;
        color: #fff;
        padding: 8px 12px;
        margin-left: 8px;
        position: relative;
        transition: color .25s ease-in-out;
        @include md {
            display: block;
            padding: 8px 20px;
            margin-bottom: 8px; }
        &:hover {
            color: $orange;
            .icon {
                fill: $orange; } } }

    .icon {
        fill: #fff;
        font-size: 16px;
        display: inline-block;
        vertical-align: middle;
        position: relative;
        top: -1px;
        transition: fill .25s ease-in-out; } }
