.widgets {
    display: flex;
    flex-wrap: wrap; }
.widget {
    $this: &;
    width: 25%;
    height: 200px;
    @include cover;
    display: flex;
    align-items: center;
    color: #fff;
    position: relative;
    @include md {
        width: 50%; }
    @include xs {
        width: 100%; }
    &:before {
        content: "";
        display: block;
        @include coverdiv;
        background: rgba(0,0,0,.5);
        z-index: 0;
        transition: opacity .25s ease-in-out; }
    &:hover {
        &:before {
            opacity: 0; }
        #{$this}__in {
            color: #fff; } }
    &__in {
        padding: 16px 0;
        position: relative;
        z-index: 1;
        color: $grey-light;
        transition: color .25s ease-in-out; }
    &__numb {
        @include rob-m;
        font-size: 64px;
        padding: 0 32px;
        @include md {
            font-size: 48px; } }
    &__desc {
        font-size: 14px;
        padding: 4px 32px 4px 32px;
        position: relative;
        background: $orange;
        border-top-right-radius: 100px;
        border-bottom-right-radius: 100px;
        display: inline-block; } }



