.mentors {
    &__list {
        display: flex;
        flex-wrap: wrap;
        padding-top: 36px;
        width: calc(100% + 24px);
        margin-bottom: 36px;
        height: 592px;
        overflow: hidden;
        transition: all .25s ease-in-out;
        &.is-open {
            height: auto; }
        @include r(420) {
            width: 100%; } } }
.mentor {
    overflow: hidden;
    width: calc(20% - 24px);
    margin: 0 24px 24px 0;
    padding: 24px;
    height: 260px;
    position: relative;
    display: flex;
    color: #fff;
    cursor: pointer;
    transform: translate3d(0,0,0);
    @include md {
        width: calc(25% - 24px); }
    @include sm {
        width: calc(33.33% - 24px); }
    @include xs {
        width: calc(50% - 24px); }
    @include r(420) {
        width: 100%;
        max-width: 220px;
        margin-left: auto;
        margin-right: auto; }
    &:before {
        content: "";
        display: block;
        @include coverdiv;
        background: $grey-light;
        z-index: -1; }
    &:after {
        content: "";
        display: block;
        @include coverdiv;
        background-image: linear-gradient(to bottom, transparent 0%, $orange 100%);
        transition: opacity .25s ease-in-out; }
    &:hover {
        &:after {
            opacity: .5; } }
    &__in {
        position: relative;
        z-index: 1;
        width: 100%;
        align-self: flex-end;
        text-align: center; }
    &__pic {
        display: block;
        height: 90%;
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%); }
    &__name {
        @include rob-r;
        font-weight: normal;
        font-size: 18px;
        letter-spacing: 1px;
        margin-bottom: 12px; }
    &__logo {
        width: 25%;
        position: absolute;
        top: 12px;
        right: 12px;
        img {
            width: 100%;
            display: block; } } }
